#App {
  text-align: center;
  background-color: #FEE500;
  position: relative;

  max-width: 800px;
  margin-right: auto;
  margin-left: auto;
  
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center; /* 세로 방향으로 가운데 정렬 */
  align-items: center; /* 가로 방향으로 가운데 정렬 */
}
